<template>
  <div>
    <!--echats -->
    <div id="energy"></div>
    <!--deta-->
    <div class="deta-energy" v-show="detaileg">
      <div class="deta-energy-wrapper">
        <div class="deta-energy-main">
          <egmain></egmain>
        </div>
      </div>
      <!--deta close -->
      <div class="deta-energy-close">
        <a @click="detaEnergyClose"></a>
      </div>
    </div>
  </div>
</template>
<script>
import infor from "../../api/ajax";
import common from "../../api/common";
import egmain from "./eyMain";
export default {
  components: {
    egmain,
  },
  created() {},
  data() {
    return {
      name: "",
      detaileg: false,
    };
  },
  methods: {
    detaEnergyClose() {
      this.detaileg = false;
    },
    createdPie() {
      var vueThis = this;
      var Container = common.getById(energy);
      common.setContentSize(Container);
      var myChart = echarts.init(Container);
      window.onresize = myChart.resize;
      var xData = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ];
      var option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
            textStyle: {
              color: "#fff",
            },
          },
        },
        grid: {
          borderWidth: 0,
          top: 110,
          bottom: 95,
          textStyle: {
            color: "#fff",
          },
        },
        legend: {
          x: "60%",
          top: "11%",
          textStyle: {
            color: "#90979c",
          },
          data: ["", ""],
        },

        calculable: true,
        xAxis: [
          {
            type: "category",
            axisLine: {
              lineStyle: {
                color: "#90979c",
              },
            },
            splitLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            splitArea: {
              show: false,
            },
            axisLabel: {
              interval: 0,
            },
            data: xData,
          },
        ],
        yAxis: [
          {
            type: "value",
            splitLine: {
              show: false,
            },
            axisLine: {
              lineStyle: {
                color: "#90979c",
              },
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              interval: 0,
            },
            splitArea: {
              show: false,
            },
          },
        ],
        dataZoom: [
          {
            show: true,
            height: 30,
            xAxisIndex: [0],
            bottom: 5,
            start: 0,
            end: 15,
            handleIcon:
              "path://M306.1,413c0,2.2-1.8,4-4,4h-59.8c-2.2,0-4-1.8-4-4V200.8c0-2.2,1.8-4,4-4h59.8c2.2,0,4,1.8,4,4V413z",
            handleSize: "110%",
            handleStyle: {
              color: "#d3dee5",
            },
            textStyle: {
              color: "#fff",
            },
            borderColor: "#90979c",
          },
          {
            type: "inside",
            show: true,
            height: 15,
            start: 1,
            end: 35,
          },
        ],
        series: [
          {
            name: "",
            type: "bar",
            barMaxWidth: 70,
            stack: "",
            itemStyle: {
              normal: {
                color: "#03FED2",
                barBorderRadius: 0,
                label: {
                  show: true,
                  position: "top",
                  formatter: function (p) {
                    return p.value > 0 ? p.value : "";
                  },
                },
              },
            },
            data: [
              37, 76, 50, 30, 80, 42, 24, 10, 101, 91, 38, 20, 37, 50, 57, 120,
              80, 42, 24, 10, 101, 91, 31, 20, 24, 10, 101, 91, 31, 20,
            ],
          },
          {
            name: "",
            type: "scatter",
            symbolSize: 15,
            symbol: "circle",
            itemStyle: {
              normal: {
                color: "rgba(252,230,48,1)",
                barBorderRadius: 0,
                label: {
                  show: true,
                  position: "top",
                  formatter: function (p) {
                    return p.value > 0 ? p.value : "";
                  },
                },
              },
            },
            data: [
              100, 193, 162, 181, 125, 119, 117, 126, 162, 143, 128, 142, 110,
              136, 129, 138, 125, 119, 117, 146, 162, 143, 128, 142, 117, 146,
              162, 143, 128, 142,
            ],
          },
        ],
      };
      myChart.setOption(option);
      //
      window.onresize = function () {
        //
        common.setContentSize(Container);
        myChart.resize();
      };

      myChart.on("click", function (params) {
        vueThis.detaileg = true;
      });
    },
  },
  mounted() {
    this.createdPie();
  },
};
</script>
<style scoped>
.deta-energy {
  position: fixed;
  z-index: 3;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(7, 17, 27, 0.9);
  top: 0;
}
.deta-energy-wrapper::after {
  display: block;
  content: ".";
  clear: both;
  height: 0;
  line-height: 0;
  visibility: hidden;
}
.deta-energy-wrapper {
  min-height: 100%;
}
.deta-energy-main {
  color: #fff;
  padding: 5%;
}
.deta-energy-close {
  position: relative;
  height: 32px;
  margin: -64px 60px;
  float: right;
  clear: both;
  font-size: 24px;
  letter-spacing: 8px;
  cursor: pointer;
}
.deta-energy-close a {
  color: #f01400;
}
</style>


